  position: relative;
}

.description-set {
  grid-template-columns: 1fr 2fr;
  margin-top: 6px;
  display: grid;
}

.description-button {
  color: #000;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 5px;
  outline: 2px solid #f74e31;
  padding: 13px 14px;
  font-size: 12px;
  font-weight: bold;
  transition: all 1s;
  position: relative;
  overflow: hidden;
}

.description-button:hover {
  color: #fff;
  cursor: pointer;
  background-color: #f74e31;
  border-color: rgba(0, 0, 0, 0);
  outline: 2px solid #70bdca;
  transform: scale(1.1);
  box-shadow: 4px 5px 17px -4px #268391;
}

.description-button:before {
  content: "";
  width: 0;
  height: 100%;
  z-index: -1;
  background-color: #f74e31;
  transition: width 1s;
  position: absolute;
  top: 0;
  left: -50px;
  transform: skewX(45deg);
}

.description-button:hover:before {
  width: 250%;
}

.description-button.active {
